.wrapper-blue {
    background: url('../img/bggreen.jpg');
    background-repeat: no-repeat;
    background-size: cover;


    .logo-letter {
        color: #25b4cd;
    }

    .checkbox-tools:checked + label {
        box-shadow: 0px 0px 15px 4px rgba(53, 150, 213, 0.71);

        .label-title {
            background: #C4FECE;

        }

        .label-summ {
            color: #25B4CD;

        }
    }
}


.btn-salat {
    background: linear-gradient(287.28deg, #80F8B8 19.47%, #38CDC5 96.71%);

    &:hover {
        background: linear-gradient(120.28deg, #80F8B8 19.47%, #38CDC5 96.71%);
    }
}
